	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
	import popupVerify from "@/components/popupVerify.vue"
	import popupMessage from "@/components/popupMessage.vue"
	let _this,timer;
	export default {
		components:{
			uniLoadMore,
			popupMessage,
			popupVerify
		},
		filters:{
			
			returnNumber:function(e){
				return e.toFixed(4)
			},
			returnStatus:function(e){
				if (e ==0){
					return '等待交易'
				}else if (e ==1){
					return '已上傳憑證'
				}else if (e == 2){
					return '已完成'
				}
				else if(e == 3){
					return '已取消'
				}
			}
		},
		
		data() {
			return {
				loadingType:'more',
				dataIndex: 1,
				pageNum: 1,
				pageSize: 20,
				items:[],
				userDetail:'',
				isFinish: 0,
				content:'',
				dealCode:''
			};
		},
		onReachBottom:function(){
			 if (timer != null) {
			            clearTimeout(timer);
			        }
			   timer = setTimeout(function() {
			        _this.fetchList();
			        }, 1000);
		},
		onShow:function(){
			this.fetchList()
			this.fetchUserDetail()
		},
		onLoad:function(){
			_this = this
			this.fetchList()
			this.fetchUserDetail()
		},
		methods:{
			async fetchUserDetail(){
				const res  = await this.$http.get('/user/details')
				console.log(res)
				this.userDetail = res.data.data
				
				
			},
			toCopy:function(data){
				uni.setClipboardData({
					data: data
				})
			},
			showDialog:function(e){
				this.dealCode = e
				this.content = `請務必登錄第三方支付賬戶確認收到該筆支付，
                                我確認已登錄收幣賬戶，並核對收幣無誤`
				this.$refs.dialog.show()
			},
			confirmInput:function(type){
				this.$refs.dialogVerify.show()
				// this.finishOrder()

			},
			confirmFinish:function(){
				
				this.finishOrder()
			},
			async fetchList(){
				if (this.loadingType == ''){
					return
				}
				let data ={
					isFinish: this.isFinish,
					pageNum: this.pageNum,
					pageSize: this.pageSize
				}
				
				const res = await this.$http.get('/udeal/udeal_list',{data: data})
				if (res.data.data.isLastPage){
					this.loadingType = ''
				}else {
					this.pageSize += 20
				}
				this.items = res.data.data.list
				
				
				console.log(res)
			},
			showPicture:function(src){
				uni.previewImage({
					urls: [src],
					success:function(){
						console.log('正在预览中')
					}
					
				})
			},
			async finishOrder(){
				let data ={
					dealCode: this.dealCode
				}
				uni.showLoading({
					title: '提交中',
					mask: true
				})
				const res = await this.$http.get('/udeal/finish',{data: data})
				uni.hideLoading()
				uni.showToast({
					title: '訂單已完成',
					duration: 1500
				});
				this.dataIndex = 2
				this.isFinish = 1
				this.items= []
				this.fetchList()
			},
			uploadPZ:function(code){
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        _this.pictures = ''
								 
						  uni.uploadFile({
							  url: 'http://test.144f.com:8080/sl-web'+'/basicdata/upload', 
							  filePath: tempFilePaths[0],
							  name:'file',
							  success: async (uploadFileRes) => {
								  console.log(uploadFileRes);	
								  _this.pictures=JSON.parse(uploadFileRes.data).data.msg
								  uni.showToast({
									title: '上传成功',
									icon:'none',
									duration: 1000
								  });
								  let data ={
									  dealCode:code,
									  proof: _this.pictures
								  }
								  const res = await this.$http.get('/udeal/upload_proof',{data: data})
								  console.log(res)
								  this.fetchList()
							  }
						 });
					},
				})	
			},
			// async delOrder(orderCode){
			// 	uni.showModal({ 
			// 		title: "友情提示",  
			// 		content: '確認要取消訂單嗎？',  
			// 		success:  async (res) => {  
			// 			if (res.confirm) {  
			// 				let data ={
			// 					orderCode: orderCode,		//买/卖单编码
			// 					buyOrSell: this.dataIndex		//1买，2卖
			// 				}
			// 				const res = await this.$http.get('/udeal/cancel_order',{data: data})
			// 				console.log(res)
			// 			}	
			// 		}  
			// 	})  
				
			// },
			selectLabel:function(e){
				this.dataIndex = e.target.dataset.index
			    
				this.pageSize = 20
				this.loadingType = 'more'
				
				if (this.dataIndex == 2){
					this.isFinish = 1
					this.fetchList()
				}
				if (this.dataIndex == 1){
                    this.isFinish = 0
					this.fetchList()
				}
			},
			
		}
		
	}
